margin {\n    &_left {\n      margin-left: 10px;\n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: 10px;\n      &_x2 {\n        margin-right: 20px;\n      }\n    }\n    &_top {\n      margin-top: 10px;\n      &_x2 {\n        margin-top: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin container-size() {\n  width: 1280px;\n  margin: auto;\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #13023c;\n  padding: 0;\n  margin: 0;\n}","@import \"./src/theme/global.scss\";\n\n$transitionHover: ease-in 200ms;\n$transitionActive: ease-in 150ms;\n\n$btn-void: $orange, $white, #ff5100d9, $white, #ff5100d1, $white, $grey, $white;\n$btn-fill: transparent, $white, #ff510047, $white, transparent, $white, $grey, $white;\n@mixin btn-style(\n  $colorBgDefault,\n  $colorDefault,\n  $colorBgHover,\n  $colorHover,\n  $colorBgActive,\n  $colorActive,\n  $colorBgDisabled,\n  $colorDisabled\n) {\n  background: $colorBgDefault;\n  color: $colorDefault;\n  &:hover {\n    background: $colorBgHover;\n  }\n\n  &:active {\n    background: $colorBgActive;\n    color: $colorActive;\n  }\n\n  &:disabled {\n    background: $colorBgDisabled;\n    cursor: not-allowed;\n    user-select: none;\n  }\n}\n\n.ux-button {\n  @include base-sizes();\n  @include base-margins();\n  height: 27px;\n  cursor: poin